<template>
  <div class="container" v-if="!pwd">
    <div class="row">
      <form class="mt-3">
        <div class="col-sm-6 m-auto">
          <div class="mb-3">
            <input
              type="text"
              class="form-control"
              id="clientname"
              placeholder="Name"
              v-model.trim="password"
            />

            <button type="button" class="btn btn-dark mt-3 w-100" @click="passwordset">
              Submit
            </button>
          </div>
        </div>
        <!--Completed Task End-->
      </form>
    </div>
  </div>


  <Home v-if="pwd"/>
</template>

<script>
import Home from "./components/Home.vue";

export default {
  name: "App",
  data() {
    return {
      pwd: "",
      password:''
    };
  },
  components: {
    Home,
  },
  mounted(){
      const bdkeylocal = localStorage.getItem("number");
      this.pwd = bdkeylocal;
  },
  methods:{
    passwordset(){
      const inputpwd = this.password;
      if(inputpwd == '4043'){
        localStorage.setItem("number",inputpwd);
        this.pwd = inputpwd;
      }
    }
  }

};
</script>

