<template>
  <div class="container">
    <div class="row">
      <div class="heading text-start">
        <h1>Update</h1>
      </div>
      <div class="col-sm-5">
        <div class="input text-start">
          <form class="mt-3">
            <div class="mb-3">
              <input
                type="text"
                class="form-control"
                id="clientname"
                placeholder="Client Name"
                v-model="clientname"
              />
            </div>
            <div class="mb-3">
              <input
                type="text"
                class="form-control"
                id="projectname"
                placeholder="Project Name"
                v-model.trim="projectname"
              />
            </div>
            <!--Completed Task-->
            <div class="completedtask">
              <p>Completed Task</p>
              <div class="mb-3">
                <input
                  type="text"
                  class="form-control"
                  id="completedtask"
                  placeholder="Completed Tasks"
                  v-model.trim="completedTask.defaultval"
                />
              </div>
              <template
                v-for="(donetask, index) in completedTask.addInputFiled"
                :key="index"
              >
                <div :class="['mb-3 d-flex addnew'+ [index]]">
                  <input
                    type="text"
                    class="form-control"
                    id="completedtask"
                    placeholder="Completed Tasks"
                    v-model.trim="completedTask.autoWrite[index]"
                  />
                  <button type="button" class="btn btn-danger ms-2" @click="removeaddnewiteam(index)">
                   Remove
                  </button>
                </div>
              </template>

              <button type="button" class="btn btn-dark" @click="addtaskdone">
                Add
              </button>
            </div>
             <!--Completed Task End-->
            
             <!--Progress Task-->
            <div class="progresstask mt-4">
              <p>Progress Task</p>
              <div class="mb-3">
                <input
                  type="text"
                  class="form-control"
                  id="progresstask"
                  placeholder="Progress Tasks"
                  v-model.trim="progressTask.defaultval"
                />
              </div>
              <template
                v-for="(donetask, index) in progressTask.addInputFiled"
                :key="index"
              >
                <div :class="['mb-3 d-flex addnew'+ [index]]">
                  <input
                    type="text"
                    class="form-control"
                    id="progresstask"
                    placeholder="Completed Tasks"
                    v-model.trim="progressTask.autoWrite[index]"
                  />
                  <button type="button" class="btn btn-danger ms-2" @click="proremoveaddnewiteam(index)">
                   Remove
                  </button>
                </div>
              </template>

              <button type="button" class="btn btn-dark" @click="proaddtaskdone">
                Add
              </button>
            </div>
             <!--Progress Task End-->
             <div class="mt-3">
                <input
                  type="text"
                  class="form-control"
                  id="taskworking"
                  placeholder="I am working on"
                  v-model.trim="workingtask"
                />
              </div>
          </form>
        </div>
      </div>
      <div class="col-sm-6 offset-sm-1">
        <div class="output text-start">
          <p>
            Hello <strong>{{ clientname }}</strong
            >,
          </p>
          <p>
            Following are the updates for <strong>{{ projectname }}</strong> as
            on {{ date }}:
          </p>
          <div v-if="completedTask.autoWrite.length !== 0 || completedTask.defaultval">
            <b>List of completed Tasks :</b>
            <ul>
              <li v-if="completedTask.defaultval">1 {{ completedTask.defaultval }}</li>
              <li class="d-block" v-for="(value,index) in completedTask.autoWrite" :key="index">{{index+2}} {{ value }}</li>
            </ul>
          </div>
          <div v-if="progressTask.autoWrite.length !== 0 || progressTask.defaultval">
            <b>List of progress tasks :</b>
            <ul>
              <li v-if="progressTask.defaultval">1 {{ progressTask.defaultval }}</li>
              <li class="d-block" v-for="(value,index) in progressTask.autoWrite" :key="index">{{index+2}} {{ value }}</li>
            </ul>
          </div>
          <div v-if="workingtask">
            <b>Working:</b>
            <p>{{ workingtask }}</p>
          </div>
          <p>Please check with the latest updates and let me know your thoughts for the same.</p>
          <p>Thanks.</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HomePage",
  data() {
    return {
      clientname: "",
      projectname: "",
      completedTask:{
        defaultval:'',
        addInputFiled:[],
        autoWrite:[]
      },
      progressTask:{
        defaultval:'',
        addInputFiled:[],
        autoWrite:[]
      },
      workingtask:''
    };
  },
  methods: {
    addtaskdone() {
      const inputfield = this.completedTask.addInputFiled.length;
      this.completedTask.addInputFiled.push(inputfield);
    },
    removeaddnewiteam(data){
      this.completedTask.addInputFiled.splice(data, 1);
      this.completedTask.autoWrite.splice(data, 1);
    },
    proaddtaskdone() {
      const inputfield = this.progressTask.addInputFiled.length;
      this.progressTask.addInputFiled.push(inputfield);
    },
    proremoveaddnewiteam(data){
      this.progressTask.addInputFiled.splice(data, 1);
      this.progressTask.autoWrite.splice(data, 1);
    }
  },
  computed: {
    date() {
      var today = new Date();
      const date =
        today.getDate() +
        ", " +
        today.toLocaleString("default", { month: "long" }) +
        ", " +
        today.getFullYear();
      return date;
    },
  },
};
</script>

